<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process249"></a>

<p><br></p>
<p><span class="rvts17">冲上云霄（</span><span class="rvts50">Up in the Clouds</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:112,Y:737</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：阿玛蒂亚神殿入口</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">探索阿尔米亚神殿</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1379.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">阿尔米亚神殿的入口位于图中的位置，需要进行一定的跳跃或传送技能，才能到达此处。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1380.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">对阿尔米亚的神像进行祈祷，可以在神殿内获得各种增益</span><span class="rvts20">BUFF</span><span class="rvts21">，包括水免疫，气免疫，火免疫等。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1381.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">阿尔米亚神殿的解密十分简单，只需要躲避各种元素伤害和陷阱，找到符文开关，并关闭陷阱。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1382.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">第一层的所有符文开关关闭后，可以激活到达第二层的传送门。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1383.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">第二处的解密也是</span><span class="rvts20">3</span><span class="rvts21">个符文开关，关闭所有的符文开关，会唤醒阿尔米亚的守护骑士。</span>
<br><span class="rvts21">一定记得不要选择</span><span class="rvts20"></span><span class="rvts21">你离开吧</span><span class="rvts20"></span><span class="rvts21">，这样会直接导致她与你的战斗。尝试选择</span><span class="rvts20"></span><span class="rvts21">你与我打斗，同时也会伤到你自己</span><span class="rvts20"></span><span class="rvts21">，接下来她会要求给你一件礼物，是个法师手套。就此，冲上云霄支线成功关闭。</span>
<br><span class="rvts21">如果你在未获得手套的情况下与这个骑士发生了战斗，那么该支线任务将不会顺利关闭。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1384.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">阿尔米亚的祭坛位于终点位置，进行祈祷可以获得她的日月派系。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



